import React from "react";
import { Container, Dropdown } from "semantic-ui-react";

//? import css
import "./NavigationBlog.css";

const NavigationBlog = () => {
  const options = [
    { key: 1, text: "Nouveauts", value: "Nouveauts" },
    { key: 2, text: "Contres indications", value: "Contres indications" },
    { key: 3, text: "Effets indsirables", value: "Effets indsirables" },
    { key: 4, text: "Remboursement", value: "Remboursement" },
    { key: 5, text: "BPF", value: "BPF" },
    { key: 6, text: "Bioquivalence", value: "Bioquivalence" },
    { key: 7, text: "Gnrique vs princeps", value: "Gnrique vs princeps" },
  ];
  return (
    <div className="_navigation blog_navigation">
      <Container>
        <ul>
          <li>Sant</li>
          <li>
            <Dropdown text="Mdicament" options={options} simple item />
          </li>
          <li>Prvention</li>
          <li>Economie de sant</li>
          <li> Industrie pharmaceutique</li>
          <li>Nutrition</li>
          <li>Mdecine dentaire</li>
          <li>Secourisme</li>
          <li>La psychologie en Algrie</li>
          <li> Hygine de vie</li>
          <li>Sport</li>
          <li>Scurit sociale</li>
          <li>Travaux et publications scientifiques</li>
        </ul>
      </Container>
    </div>
  );
};

export default NavigationBlog;
